<template>
    <div class="all-wrapper" id="app" v-show="isUserLoad">
        <header :class="{'header header--black':  isDetail, 'header-moderator': !isDetail}">
            <div class="header-main" :class="{'header-main--editor':  isDetail}" >
                <div class="inner-wrapper">
                    <div class="h-left">
                        <div class="h-logo">
                            <router-link class="h-logo-link" to="/buh/">
                                <span class="icon"></span>
                                <span class="t">buhgalter</span>
                            </router-link>

                            <tooltip ref="tooltipNotifyReset"
                             :direction="'bottom-center-direction'"
                             :add-class-wrap="'h-logo__tooltip'"
                             :isBtnClose="false"
                             :add-style="{width: 175 + 'px'}">
                                <span slot="tooltipBtn"></span>
                                
                                <div slot="tooltipContent">
                                    <div class="text-center">
                                        <div>
                                            <a href="" class="i i-refresh2 i--blue"></a>
                                        </div>
                                        <div class="text-size-s" style="margin-top: 4px; ">
                                            Мы обновили сайт.<br>
                                            Чтобы все заработало –<br> 
                                            <a href="">перезагрузите</a> страницу
                                        </div>
                                    </div>
                                </div>
                            </tooltip>
                        </div>
                        
                        
                        <div v-if="isDetail&&!isLoadDetail" class="h-editor-back">
                            <router-link :to="buhBackUrl"
                                         class="h-editor-link">
                                <span class="i i-back"></span>
                                <span class="t">{{buhBackName}}</span>
                            </router-link>
                        </div>
                    </div>
                    
                    
                    
                    
                    <div class="h-right" v-if="!isDetail">
                        <partner-search></partner-search>
                        
                        <div>
                            <tooltip v-if="user.firstName"
                                     :direction="'bottom-right-direction'"
                                     :add-class-wrap="'h-user'"
                                     :add-class-btn="'l'"
                                     :add-class="'h-user-menu'"
                                     :is-padding="false">
                                <span slot="tooltipBtn">
                                    <span class="t">
                                        {{ user.firstName.charAt(0) }}
                                    </span>
                                </span>
                                
                                <div slot="tooltipContent">
                                    <div class="h-user-menu__wrap">
                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-man2"></span>
                                            </span>
                                            <span class="t">{{ user.lastName }} {{ user.firstName }} {{ user.secondName }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-phone"></span>
                                            </span>
                                            <span class="t">{{ user.phone }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-mail"></span>
                                            </span>
                                            <span class="t">{{ user.email }}</span>
                                        </div>

                                        <div class="h-user-menu__item">
                                            <span class="icon">
                                                <span class="i i-marker"></span>
                                            </span>
                                            <span class="t">{{ currentCityIp.name }}</span>
                                        </div>

                                    </div>

                                    <div class="h-user-menu__bottom">
                                        <a href="javascript:void(0);" class="link" @click="logout()">
                                            <span class="i i-exit2"></span>
                                            <span class="t">ВЫЙТИ</span>
                                        </a>
                                    </div>
                                </div>
                            </tooltip>
                        </div>
                    </div>
                    
                    
                    <div v-else class="h-right">
                        <div class="h-right__editor" v-if="!isLoadDetail">
                            <span @click="kick" class="btn h-editor-btn btn--ico">
                                <span class="i i-kick"></span>
                                <span class="t">Пнуть</span>
                            </span>
                            
                            <span @click="(provider.provider.isBan)?unban():ban()" class="btn h-editor-btn btn--ico">
                                <span class="i" :class="{'i-ok2':provider.provider.isBan, 'i-stop':!provider.provider.isBan}"></span>
                                <span class="t">{{(provider.provider.isBan)?'Разблокировать':'Заблокировать'}}</span>
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </header>
        
        
        
        <main id="main">
            <router-view v-if="isBuh"/>
            
            <section v-else>
                <div class="content">
                    <div class="main-title">
                        <div class="main-title__wrap main-title--nowrap">
                            <div class="h1">Личный кабинет Бухгалтера</div>
                        </div>
                        <div class="main-title__right"></div>
                    </div>
                    
                    
                    <section class="container">
                        <auth v-if="!token && !isBanned && isValidPhone"></auth>
                        
                        <div class="wrapper-content" v-else-if="isBanned">
                            <div class="sect-blank">
                                <div class="wrap text-center">
                                    <div class="text-size-l text-bolder text-color-red mt16">Слишком много попыток входа!</div>
                                    <div class="mt">Вы заблокированы на 12 часов.</div>
                                    <div class="mt">Если у вас что-то срочное, <a href="javascript:void(0);" @click="openMailZems">напишите</a> руководителю проекта</div>
                                </div>

                                <div class="mini-zems"></div>
                            </div>
                            <!-- end sect-blank -->
                        </div>
                        
                        
                        <div class="wrapper-content" v-else>
                            <div class="sect-blank">
                                <div class="wrap text-center">
                                    <div class="text-size-l text-bolder text-color-red mt16">
                                        У номера  {{ user.phone ? user.phone : formAuthFields.login.value }}<br>
                                        нет доступа в этот раздел
                                    </div>
                                </div>
                            </div>
                            <!-- end sect-blank -->
                        </div>
                    </section>
                </div>
            </section>
        </main>
        <vue-footer :hide-offer="true"></vue-footer>
    </div>
</template>
<script>
    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';
    import PartnerSearch from '@/components/auditor/PartnerSearch.vue';
    import Auth from '@/components/user/auth/AuthPersonal.vue'
    import VueFooter from '@/components/VueFooter.vue'

    export default {
        components: {
            Tooltip,
            PartnerSearch,
            Auth,
            VueFooter
        },
        created() {
            this.$store.dispatch('users/getCurrent');
        },
        computed: {
            isBuh(){
                if(this.user.access_rights){
                    return this.user.access_rights.is_buh;
                }
                else{
                    return false;
                }  
            },
            isDetail(){
                if(this.user.access_rights && this.user.access_rights.is_buh){
                    return this.$route.name === 'buh-provider-detail'
                }
                else{
                    return false;
                }
            },

            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            ...mapState({
                isUserLoad: state => state.users.isLoaded,
                user: state => state.users.user,
                isValidPhone: state => state.auth.isValidPhone,
                formAuthFields: state => state.auth.forms.auth.fields,
                currentCityIp: state => state.cities.currentCityIp,
                isBanned: state => state.auth.isBanned,
                provider: state => state.auditorProviders.itemDetail,
                isLoadDetail: state => state.auditorProviders.isLoadDetail,
                popups: state => state.popups.items,
            }),
            buhBackName(){
                if(this.provider.provider.isBan){
                    return "Заблокированные поставщики";
                }
                else{
                    return "Активные поставщики";
                }
            },
            buhBackUrl(){
                if(this.provider.provider.isBan){
                    return '/buh/blocked/';
                }
                else{
                    return '/buh/';    
                }
            }
        },
        methods: {
            logout() {
                this.$store.dispatch('auth/logout');
                this.$store.commit('auth/setStep',{ step:1 })
                //this.$store.commit('partner/setLoaded',false)
                /*this.$store.commit('partner/setCurrentField', {
                    name: 'success',
                    value:false,
                })*/
            },
            unban(){
                this.$store.dispatch('popups/open', {name: this.popups.auditorProviderCancelBlock.name});
            },
            ban(){
                this.$store.dispatch('popups/open', {name: this.popups.auditorProviderBlock.name});
            },
            kick(){
                this.$store.dispatch('popups/open', {name: this.popups.kickPartner.name});
            },
            showNotifyPageReset(){
                this.$refs.tooltipNotifyReset.tooltipShow();
            }
        },
        mounted(){
            this.$store.subscribe((mutation, state) => {
                if(mutation.type=='popups/open'){
                    if(state.popups.items.notifyPageReset.show==true){
                        this.showNotifyPageReset();   
                    }
                }  
            });
        },
    }
</script>
<style lang="sass">
    body
</style>