import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
        path: '/buh/id:id/',
        name: 'buh-provider-detail',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: function () {
            return import(/* webpackChunkName: "about" */ '../views/buh/ProviderDetail.vue')
        },
        //beforeEnter: ifModerator,
    },
    {
        path: '/buh/blocked/',
        name: 'buh-providers-list-blocked',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: function () {
            return import(/* webpackChunkName: "about" */ '../views/buh/ProvidersList.vue')
        },
    },
    {
        path: '/buh/search/',
        name: 'buh-providers-list-search',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: function () {
            return import(/* webpackChunkName: "about" */ '../views/buh/ProvidersList.vue')
        },
    },
    {
        path: '/buh/',
        name: 'buh-providers-list',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: function () {
            return import(/* webpackChunkName: "about" */ '../views/buh/ProvidersList.vue')
        },
    },

  ]
})
