<template>
    <div ref="searchField" class="header-category__search">
        <div class="field-wrap">
            <input v-on:keyup.enter="submit" ref="searchFieldInput" type="text" class="field" placeholder="Поиск поставщиков" v-model="search">
            <button class="b" @click="searchClick"></button>
        </div>
        
        <div ref="searchFieldBtn" @click="openSearchField" class="btn-search"></div>
    </div>
</template>



<script>

    import {mapState} from 'vuex';

    export default {
        name: "ProvidersSearch",
        components: {
        },
        data: function() {
            return {
                search: '',
            }
        },
        computed: {
            ...mapState(
                {
                    menuList: state => state.menus.itemsAction,
                }),
            defaultStatus() {
                return this.menuList['providers'][0]['id'];
            },
            currentStatus() {
                return this.$route.params.action ? this.$route.params.action : this.defaultStatus;
            }
        },
        methods: {
            searchClick() {
                //this.$store.dispatch('moderatorProviders/getItems', {status: this.currentStatus, search: this.search, reload: true});
            },
            submit() {
                this.$store.dispatch('auditorProviders/getItems', {reload: true, search: this.search});
            },
            openSearchField: function(){
                this.$refs.searchField.classList.add('open');
                this.$refs.searchFieldInput.focus();
            },
            hideSearchField: function() {
                this.$refs.searchField.classList.remove('open');
            }
        },
        created: function() {
            document.addEventListener('click', e => {
                let target = e.target;
                let search = this.$refs.searchField;
                if (typeof (search) !== 'undefined') {
                    let itsSearch = target == search || search.contains(target);

                    if (!itsSearch) {
                        this.hideSearchField();
                    }
                }
            })
        }
    }
</script>
<style scoped>
    .header-category__search {
        height: 56px;
        margin-right: 16px;
    }
    
    .header-main .header-category__search .field {
        width: 225px;
        height: 56px;
    }
</style>