import Vue from 'vue'
import Vuex from 'vuex'
import menus from './modules/menus'
import popups from './modules/popups'
import auth from './modules/auth'
import users from './modules/users'
import cities from './modules/cities'
import auditorProviders from './modules/buh/providers'
import moderatorTemplates from './modules/moderator/templates'
import options from './modules/options'

Vue.use(Vuex)

export default new Vuex.Store({
    strict: process.env.NODE_ENV !== 'production',
    modules: {
        menus,
        popups,
        auth,
        users,
        cities,
        auditorProviders,
        options,
        moderatorTemplates
        //auditorRepayments
    },
    state: {
        onTabClick: false,
        notifyPageVersion: '', 
    },
    mutations: {
        setTabClick(state, data){
            state.setTabClick = true;
        },
        setNotifyPageVersion(state, data){
            state.notifyPageVersion = data;
        },
    },
    actions: {

    }
})
