import axios from 'axios'
import {jsonHeader,wwwFormHeader} from '@/helpers/axiosHeaders'
import Vue from 'vue'
import qs from "qs";

const state = {
    items: [],
    itemDetail: {
        provider: [],
        repayments: [],   
        bills: [],
        request: [],
        coupons: [],
        stat: [],
        history: [],
    },
    sortList:{
        order:'down',
        by:'name',
    },
    isLoad: false,
    isLoadDetail: false,
    isLoadNextPage: true,
    page: 1,
    pageType: 'default',
    searchText: '',
    templates: [],
}
// getters
const getters = {
    getTotalCommission: state => {
        let sum = 0;
        for(let item of state.itemDetail.repayments) {
            sum += item.our_no_format
        }
        return sum;
    },
}
const mutations = {
    setItems(state, data) {
        if(typeof data === 'object' && data.items !== undefined) {
            //Vue.set(state,'items', data.items);
            for (let itemIndex in data.items) {
                let item = data.items[itemIndex];
                state.items.push(item); 
            }
        }
    },
    setNewPage(state){
        Vue.set(state,'items', []);     
        state.page = 1;
        state.isLoadNextPage = true;
    },
    setLoad(state, data) {
        state.isLoad = data;
    },
    setLoadDetail(state, data) {
        state.isLoadDetail = data;
    },
    setPageType(state, data) {
        state.pageType = data.pageType;
        if(data.text){
            state.searchText = data.text;
        }
        else{
            state.searchText = '';   
        }
    },
    incrementPage(state) {
        state.page++;
    },
    setItemDetailEmpty(){
        state.itemDetail = {
            provider: [],
            repayments: [],   
            bills: [],
            request: [],
            coupons: [],
            stat: [],
            history: [],
        }
    },
    setItemDetail(state, data){
        state.itemDetail = {
            provider: [],
            repayments: [],   
            bills: [],
            request: [],
            coupons: [],
            stat: [],
            history: [],
        }
        if(data.repayments){
            state.itemDetail.repayments = data.repayments;   
        }
        if(data.provider){
            state.itemDetail.provider = data.provider;   
        }
        if(data.bills){
            state.itemDetail.bills = data.bills;   
        }
        if(data.request){
            state.itemDetail.request = data.request;   
        }
        if(data.coupons){
            state.itemDetail.coupons = data.coupons;   
        }
        if(data.stat){
            state.itemDetail.stat = data.stat;   
        }
        if(data.history){
            state.itemDetail.history = data.history;   
        }
        if(data.templates){
            state.templates = data.templates;
        }
    },
    isLoadNextPage(state, data) {
        state.isLoadNextPage = data;
    },
    setSort(state, data){
        state.sortList.by = data.by;
        state.sortList.order = data.order;
    }
}
const actions = {
    setSort(context,data){
        let sortBy = state.sortList.by, sortOrder = state.sortList.order;
        if(sortBy == data.name){
            sortOrder = (state.sortList.order=='up')?'down':'up';   
        }
        else{
            sortBy = data.name;   
        }

        context.commit('setSort', {by: sortBy, order: sortOrder}); 
        context.dispatch('getItems',{reload: true,});
    },
    getItems(context,data) {
        return new Promise((resolve, reject) => {
        context.commit('setItemDetailEmpty');
        if(!state.isLoad){
            if(data.reload){
                context.commit('setNewPage', {status: data.status});    
            }
            if(state.isLoadNextPage){
                context.commit('setLoad', true);
                
                if(data.search){
                    context.commit('setPageType', {pageType: 'search', text: data.search});
                }
                else if(data.blocked){
                    context.commit('setPageType', {pageType: 'blocked'});  
                }
                else{
                    context.commit('setPageType', {pageType: 'default'});
                }
                axios.get(process.env.VUE_APP_API_PATH+'/auditor/providers/',{
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                    withCredentials: true,
                    params: {
                        blocked: data.blocked,
                        search: data.search,
                        page: state.page,
                        sortBy: state.sortList.by,
                        sortOrder: state.sortList.order,
                    },
                    paramsSerializer: function(params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                }).then(response => {
                    context.commit('setLoad', false);
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setItems', response.data.result);
                        context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                        }
                    }
                    context.commit('incrementPage');
                    resolve();

                }).catch(function (error) {
                    context.commit('setLoad', false);
                    resolve();
                });
            }
        }
    });
    },
    getItem(context,data) {
        if(!state.isLoadDetail){
            context.commit('setLoadDetail', true);
            axios.get(process.env.VUE_APP_API_PATH+'/auditor/providers/'+data.id+'/',{
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
            }).then(response => {
                context.commit('setLoadDetail', false);
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('setItemDetail', response.data.result);
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
                context.commit('setLoadDetail', false);
            });
        }
    },
    sendForm(context, data){
        return new Promise((resolve, reject) => {
            axios.put(process.env.VUE_APP_API_PATH_MB+'/public/mb/v1.php',{},{
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                withCredentials: true,
                params: {
                    ...data,
                    ID: data.id,
                    GROUP: 'auditor',
                    RESOURCE: 'providers',
                },
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
            }).then(response => {
                if(typeof response.data === 'object' && +response.data.error === 0) {
                    resolve();
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
            });
        });
    },
}
export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}